.ch-par {
  height: 300px;
  width: auto;
  margin-top: 20px;
  position: relative;
}

.ch-par .tool-tip {
  opacity: 0;
  transition: all 250ms ease;
}

.ch-par .tool-tip-one {
  top: 20px;
  left: 10px;
  background-color: #FFFFFF;
  position: absolute;
  min-width: 80px;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  z-index: 10;
  box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.25);
}

.ch-par .tool-tip-one:before, .ch-par .tool-tip-one:after {
  content: "";
  position: absolute;
  border-left: 10px solid transparent;
  border-right: 5px solid transparent;
  top: 70%;
  left: 50%;
  margin-left: -10px;
  transform: rotate(-45deg);
}

.ch-par .tool-tip-one:before {
  border-top: 40px solid rgba(0, 0, 0, 0.25);
  margin-top: 1px;
  margin-left: -9px;
  z-index: -1;
}

.ch-par .tool-tip-one:after {
  border-top: 40px solid #FFFFFF;
  z-index: 1;
}

.ch-par .tool-tip-one-text {
  padding: 10px;
  background-color: #FFFFFF;
  color: black;
  font-size: 14px;
  font-weight: 700;
  z-index: 100;
}

